<template>
    <div>
        <div class="chenter">
         <div class="top">
        <van-icon name="arrow-left" @click="fanhui()"/>
        <p>核对订单</p>
        </div>
        <!-- 地址 -->
        <div class="dizhi" @click="dizi" v-show="$store.state.pays">
        <span class="iconfont icon-dingwei" ></span>
        <p>您还没有收货地址哦，请点击新增吧~</p>
        <van-icon name="arrow" />
        </div>
         <zujian  v-show="!$store.state.pays"></zujian>
        <!-- 地址 -->
        <!-- 商品-->
        <div class="sp" @click="label">
            <div class="left">
                <div class="tup" v-for="(item, index) in $store.state.pay[0]" :key="index">
                    <img :src="item.src" alt="">
                </div>
            </div>
            <p>共{{jianchu}}件</p>
            <van-icon name="arrow" />
        </div>
        <!-- 商品-->
        <!-- 商品信息 -->
        <div class="xinxi">
            <p>总件数<span>共{{jianchu}}件</span></p>
            <p>商品总额 <span>￥{{jiage}}</span></p>
            <p>优惠券抵扣 <span>0.00</span></p>
            <p>运费<span>￥0.00</span></p>
        </div>
        <!-- 商品信息 -->
        </div>
<van-submit-bar
  button-text="提交订单"
  @submit="onSubmit"
>
    <div class="xu">
    <p><span>需支付</span>￥{{jiage}}</p>
    </div>
</van-submit-bar>
    </div>
</template>

<script>
import { ActionSheet } from 'vant';
import zujian from "@/components/zujian.vue"
    export default {
        data() {
            return {
                ischeng:true
            }
        },
        methods:{
            fanhui(){
                this.$router.push('/ShoppingCart')
            },
            label(){
                this.$router.push('/Label')
            },
            dizi(){
                this.$router.push('/Family')
            },
            onSubmit(){
                if(!this.$store.state.pays){
                      this.$router.push('/Check');
                      this.$store.commit('onubmit')
                }else{
                     this.$dialog.alert({
                        message: '请先填写地址'
                        });
                }
            }
        },
        computed: {
            jianchu(){
                let numerical = 0;
                this.$store.state.pay[0].forEach(element => {
                    numerical += element.sl
                });
                return numerical;
            },
             jiage(){
                let price = 0;
                this.$store.state.pay[0].forEach(element => {
                    price += element.sl *element.jg
                });
                return price;
            }
        },
        components:{
            zujian,
        },
    }
</script>

<style lang="scss" scoped>
.chenter{
    background: #f5f5f5;
}
     .top{
        width: 100%;
        text-align: center;
        border-bottom: 1px solid #ddd;
        background: #fff;
        position: relative;
        .van-icon{
            width: 20px;
            height: 20px;
            position: absolute;
            top: 3px;
            left: 10px;
        }
        p{
            margin: 0;
            margin: 10px 0;
        }
    }
    .dizhi{
        background: #fff;
        display: flex;
        align-items: center;
        padding: 20px 10px;
        box-sizing: border-box;
        position: relative;
        margin-bottom: 10px;
        span{
            font-size: 22px;
            color: #666;
        }
        p{
            margin: 0;
            font-size: 15px;
        }
        .van-icon{
            position: absolute;
            right: 15px;
            color: #666;
        }
    }
   
    .sp{
        width: 100%;
        display: flex;
        align-items: center;
        background: #fff;
        border-bottom: 1px solid #ddd;
        position: relative;
         box-sizing: border-box;
         padding: 10px;
         margin-bottom: 10px;
         .left{
             width: 80%;
             height: 60px;
             overflow: hidden;
             img{
                 width: 25%;
                 float: left;
             }
         }
         p{
              margin: 0;
            font-size: 15px;
         }
          .van-icon{
            position: absolute;
            right: 15px;
            color: #666;
        }
    }
    .xinxi{
        background: #fff ;
        padding: 10px;
        box-sizing: border-box;
        p{
            margin: 0;
            padding: 10px 0;
            font-size: 15px;
            position: relative;
            span{
                position: absolute;
                right: 5px;

            }
        }
    }
    .van-submit-bar{
        position: fixed;
        bottom: 0;
     .xu{
         position: absolute;
         left: 10px;
         p{
             margin: 0;
             font-size: 14px;
             span{
                 color: #999;
                 font-size: 12px;
                 margin-right: 5px;
             }
         }
     }
    }
</style>