<template>
  <div id="app">
    <router-view />
    <van-tabbar v-model="active" class="tab" v-show="ischeng">
      <!-- <router-link name="home" icon="home-o to="/zyy">标签</router-link> -->
      <router-link to="/Home" class="van-tabbar-item van-tabbar-item--active">
        <div class="van-tabbar-item__icon">
          <i class="van-icon van-icon-home-o"></i>
        </div>
        <div class="van-tabbar-item__text">主页</div>
      </router-link>
      <router-link to="/Classification" class="van-tabbar-item van-tabbar-item--active">
        <div class="van-tabbar-item__icon">
          <i class="van-icon van-icon-apps-o"></i>
        </div>
        <div class="van-tabbar-item__text">分类</div>
      </router-link>
      <router-link to="/ShoppingCart" class="van-tabbar-item van-tabbar-item--active">
        <div class="bao" @click.prevent="gw">
          <div class="qu">
            <div class="van-tabbar-item__icon">
              <i class="van-icon van-icon-cart-o"></i>
            </div>
            <div class="van-tabbar-item__text">购物</div>
          </div>
        </div>
      </router-link>
      <router-link to="/My" class="van-tabbar-item van-tabbar-item--active">
        <div class="bao" @click.prevent="pd">
          <div class="qu">
            <div class="van-tabbar-item__icon">
              <i class="van-icon van-icon-friends-o"></i>
            </div>
            <div class="van-tabbar-item__text">我的</div>
          </div>
        </div>
      </router-link>
    </van-tabbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ischeng: false,
      active: "home"
    };
  },
  methods: {
    pd() {
      if (!localStorage.dengl) {
        this.$router.push("/Login");
      } else {
        this.$router.push("/My");
      }
    },
    gw() {
      if (!localStorage.dengl) {
        this.$router.push("/Login");
      } else {
        this.$router.push("/ShoppingCart");
      }
    }
  },
  created() {
    this.$router.push("/");
  },
  watch: {
    "$route.path": function(newValue, oldValue) {
      if (
        newValue === "/Advertising" ||
        newValue === "/" ||
        newValue === "/Search" ||
        newValue === "/ShoppingCart" ||
        newValue === "/Login" ||
        newValue === "/Details" ||
        newValue === "/Registered" ||
        newValue === "/Login" ||
        newValue === "/Details" ||
        newValue === "/Label" ||
        newValue === "/Pay" ||
        newValue === "/Family" ||
        newValue === "/Check" ||
        newValue === "/yuyue" ||
        newValue === "/Show"

      ) {
        this.ischeng = false;
      } else {
        this.ischeng = true;
      }
      if (newValue == oldValue) {
        this.ischeng = true;
      }
    }
  }
};
</script>
