<template>
  <div>
         <div class="top">
        <van-icon name="arrow-left" @click="fanhui()"/>
        <p>选择收货地址</p>
        </div> 
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      @add="onAdd" 
      @click-item="item"
    />
          <!-- @add="onAdd" -->
      <!-- @edit="onEdit" -->
  </div>
</template>

    <script>
export default {
  data() {
    return {
      chosenAddressId: "1",
      list: [

      ],
 
    };
  },

  methods: {
    onAdd() {
        this.$router.push('/Family')
    },

    onEdit(item, index) {

    },
    item(item,index){
      this.$store.commit('items',{
        address:item.address,
        name:item.name,
        tel:item.tel,
      })
        this.$router.push('/Pay')
    },
    fanhui(){
      this.$router.push('/Pay')
    }
  },
    
  created(){
    this.$store.state.arr.forEach(element => {
        this.list.push(element)
      });
      
  }
  
};
</script>

<style lang="scss" scoped>

     .top{
        width: 100%;
        text-align: center;
        border-bottom: 1px solid #ddd;
        background: #fff;
        position: relative;
        .van-icon{
            width: 20px;
            height: 20px;
            position: absolute;
            top: 3px;
            left: 10px;
        }
        p{
            margin: 0;
            margin: 10px 0;
        }
    }
</style>