<template>
  <div>
    <div class="cont">
      <img src="../assets/images/timg.jpg" alt="">
    <van-button class="btn" round type="info">{{num}}秒后跳转</van-button>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        num:3,
        cleardata:'nell'
      }
    },
    created() {
       this.cleardata = setInterval (()=>{
          if(this.num==0){
            this.$router.push('/Home')
          }else{
            this.num--
          }
        },1000)
    },
    beforeDestroy() {
        clearInterval(this.cleardata)
    },
  }
</script>

<style scoped>
.cont{
  width: 100%;
  height: 100%;
}
img{
  width: 100%;
  height: 100%;
  display: block;
}
  .btn{
    background: transparent;
    position: fixed;
    top: 10px;
    right: 30px;

    border:1px solid rgba(255,255,255,0.5);
  }
</style>
