<script>
// import { request } from 'http';
// import axios from "axios";
import { Notify } from 'vant';
export default {
  data() {
    return {
      username: "",
      password: "",
      ischeng: true
    };
  },
  methods: {
    zh() {
      if (this.ischeng == true) {
        this.ischeng = false;
      } else {
        this.ischeng = true;
      }
    },
    fh() {
      this.$router.push('/Home');
    },
    wj(){
        this.$router.push('/Registered')
    },
