      // if(this.username==this.$store.state.username && this.password==this.$store.state.password){
      //    localStorage.dengl = 1433223;
      //     this.$router.push("/wd");
      // }
      this.$axios.get("http://182.61.34.192:3000/login", {
          params: {
            username: this.username,
            password: this.password
          }
        })
        .then(val => {
          if (val.data.code == 200) {
            let lp = val.data.result;
            this.$router.push("/My");
            localStorage.dengl = lp;
          } else {
             Notify({
                            message: '登录失败',
                            color: '#F8A1A4',
                            duration: 1000
                        });
          }
          console.log(lp);
        })
        .catch(error => {
          console.log(error);
        });
    },
    zuce() {
      this.$router.push("/Registered");
    }
  }
};
