<template>
    <div>
       
    <div class="bq" v-for="(item,i) in $store.state.splb" :key="i">
        <div class="bao">
        <div class="left">
            <div class="div">
            <div class="an" @click='zh(i)'><van-icon  v-show="item.ischeng" color="#fff" name="success"/></div>
            </div>
            <img :src=item.src alt="">
            
        </div>
        <div class="right">
            <p>{{item.name}}</p>
            <span>{{item.gg}}</span>
            <div class="biaoq">
                <i>￥{{item.jg * item.sl}}</i>
                <div class="btn"> 
                    <button @click="jian(i)">-</button>
                <button>{{item.sl}}</button>
                <button @click="jia(i)">+</button>
                </div>
               
            </div>
        </div>
        
    </div>
    </div>
    </div>
</template>

<script>
    export default {
        data(){
       return{
       }
    },
    methods:{
        zh(i){
            if(this.$store.state.splb[i].ischeng==true){
                this.$store.state.splb[i].ischeng=false
            }else{
                this.$store.state.splb[i].ischeng=true
            }
            
        },
        jian(i){
            if(this.$store.state.splb[i].sl>1){
                this.$store.state.splb[i].sl--
            }   
        },
        jia(i){
            this.$store.state.splb[i].sl++   
        }
    }
    }
</script>

<style lang="scss" scoped>
    .bq{
        width: 100%;

        padding-bottom: 15px;
        border-bottom: 1px solid #ddd;
        background: #fff;
       .bao{
            display: flex;
            width: 100%;
            padding-top: 10px;
        .left{
            width: 40%;
            position: relative;
            display: flex;
            .div{
                position: relative;
                width: 20px;
                margin:0 15px;
                .an{
                    width: 20px;
                    height: 20px;
                    position: absolute;
                    top: 50%;
                    // border: 1px solid #ddd;
                    background: #000;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                }
            }
            
            img{
                width: 80px;
                overflow: hidden;
                background: transparent;
            }
        }
        .right{
            width: 60%;
            p{
                margin: 0;
                font-size: 14px;
                margin-bottom: 10px;
            }
            span{
                color: #9d9d9d;
                font-size: 12px;
                margin-bottom: 5px;
            }
            .biaoq{
                position: relative;
                i{
                    font-size: 13px;
                }
                .btn{
                     position: absolute;
                    top: 0;
                    right: 5px;
                    button{
                    background: none;
                    outline: none;
                    border: 1px solid #ddd;
                }
                }
                
            }
        }
        }
        }
</style>