<template>
    <div>
        <div class="top">
        <van-icon name="arrow-left" @click="fh"/>  
        <h1>注册</h1>  
        <van-cell-group>
  <van-field
    v-model="username"
    clearable
    label="用户名"
    placeholder="请输入用户名"
  />
  <van-field
    v-model="password"
    type="password"
    label="密码"
    placeholder="请输入密码"

  />
  <van-field
    v-model="passwords"
    type="password"
    label="密码"
    placeholder="请输入密码"
  />
  <van-field
            v-model="iphone"
            required
            clearable
            label="手机号码"
            placeholder="请输入手机号码"
        />
  <van-field
    v-model="sms"
    center
    clearable
    label="短信验证码"
    placeholder="请输入短信验证码"
  >
    <van-button slot="button" size="small" type="primary">发送验证码</van-button>
  </van-field>
</van-cell-group>
<van-button type="primary" size="large" color="#999" @click='qd'>同意条款并注册</van-button>
<div class="gang"></div>
 <div class="yd">
          <div class="an" @click="zh">
            <van-icon v-show="ischeng" color="#fff" name="success" />
          </div>
          <span>已阅读《丝芙兰用户服务协议》和《丝芙兰隐私政策》</span>
        </div>
</div>
    </div>
</template>

<script>
import { Notify } from 'vant';
    export default {
        data() {
            return {
                username:'',
                password:'',
                passwords:'',
                sms:'',
                iphone:'',
                ischeng: false,
            }
        },
        methods:{
            fh(){
                this.$router.push('/Login')
            },
            zh() {
            if (this.ischeng == true) {
                this.ischeng = false;
            } else {
                this.ischeng = true;
            }
            },
            qd(){
                if(this.ischeng==true){
                    if(this.username!='' && this.password!='' && this.passwords!='' && this.sms!=''){
                        if(this.password== this.passwords){
                            this.$axios.get("http://182.61.34.192:3000/register",{
                        params:{
                            username:this.username,
                            password:this.password,
                        }
                    }).then((data)=>{
                        if(data.data.code == 200){
                            Notify({
                                message: '注册成功',
                                type: 'success',
                                duration: 2000,
                                onOpened:()=>{
                                    this.$router.push("/Login");
                                }
                            });
                        }
                    })
                        }else{
                            this.$toast('密码不一');
                        }
                        
                    }else{
                        this.$toast('不能为空');
                    }
                }else{
                    this.$toast('请先勾选');
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
#app{
margin: 0;
}
.top{
    width: 100%;
    height: 640px;
    padding: 10px;
    box-sizing: border-box;
    background: url(../assets/images/zuce.jpg) no-repeat;
    background-size: 100% 100%;
}
h1{
    margin: 0;
    font-weight: 400;
    font-size: 30px;
    text-align: center;
    margin-bottom: 15px;
}
.van-button--large{
    margin: 20px 0;
}
 .gang {
    width: 20px;
    height: 1px;
    background: #474747;
    margin: 0 auto;
    opacity: 0.9;
  }
  .yd {
    display: flex;
    width: 100%;
    margin-top: 50px;
    .an {
      width: 20px;
      height: 20px;
      position: relative;
      // border: 1px solid #ddd;
      margin-left: 10px;
      background: #000;
                    display: flex;
                    justify-content: center;
                    align-items: center;
      // background: #000;
      // .van-icon-success {
      //   width: 100%;
      //   height: 100%;
      //   position: absolute;
      //   top: 0;
      //   left: 0;
      //   // color: #fff;
      // }
    }
    span {
      font-size: 12px;
      margin-left: 10px;
    }
  }
</style>