import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    xyl: [],
    newis: true,
    splb: [],
    qe: 0,
    pay: [],
    arr: [],
    pays: true,
    addres: [
      {
        tel: "",
        name: "",
        address: ""
      }
    ],
    indexs: 0,
    check: false
  },
  mutations: {
    add(state, i) {
      state.xyl.push(i);
      if (state.xyl.length > 1) {
        state.xyl.shift();
      }
    },
    tdl(state) {
      state.newis = false;
      let index = state.splb.findIndex(value => value.id == state.xyl[0].id);
      if (index == -1) {
        state.splb.push(state.xyl[0]);
      } else {
        state.splb[index].sl += 1;
      }
    },
    onSubmit(state, i) {
      state.pay = [];
      state.pay.push(i);
    },
    onSave(state, i) {
      state.arr.push(i);
    },
    items(state, i) {
      state.pays = false;
      state.addres[0].tel = i.tel;
      state.addres[0].name = i.name;
      state.addres[0].address = i.address;
    },
    dingd(state, i) {
      state.indexs = i + 1;
    },
    onubmit(state) {
      state.check = true;
    }
  },
  actions: {},
  modules: {}
});
