<template>
  <div>
    <div class="swiper-container">
        <div class="swiper-wrapper">
            <div class="swiper-slide">
              <img class="animated bounce" v-show="flag" src="../assets/images/yd1.jpg" alt="">
            </div>
            <div class="swiper-slide">
              <img class="animated bounce" v-show="flag" src="../assets/images/yd2.jpg" alt="">
            </div>
            <div class="swiper-slide san">
              <img class="animated bounce " v-show="flag" src="../assets/images/yd3.jpg" alt="">
              <van-button plain type="info" class="btn" @click="add">立即体验</van-button>
            </div>
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
import Swiper from "swiper/dist/js/swiper.js";
export default {
  data(){
      return{
        flag:true,
      }
  },
  methods: {
    add(){
        this.$router.push('/Advertising')  
    }
  },
  created() {
    if(localStorage.one) {
                this.$router.push("/Advertising");
            }else{
                localStorage.one = true;
            }
  },
     mounted() {
       let _this=this;
       new Swiper('.swiper-container', {
        pagination: {
          el:'.swiper-pagination'
        },
        on: {
                    slideChangeTransitionStart: function(){
                        _this.flag = false;
                    },
                    slideChangeTransitionEnd: function(){
                        _this.flag = true;
                        // alert(this.activeIndex);//切换结束时，告诉我现在是第几个slide
                    },
                },
    });
     },
};
</script>

